.App {
  text-align: center;
}
.header_link {
  margin-right: 4.1%;
  padding: 10px;
  text-decoration: none;
  font-family: Oswald;
  font-style: normal;
  font-size: 32px;
  font-weight: bold;
  cursor: pointer;
  background-color: #021311;
  outline: none;
  border: none;
  color: #707070;
}
.resumeButton{
  text-decoration: none;
  border-radius: 15px;
  background-color: #00262E;
  border: 2px solid #0F786B;
  cursor: pointer;
  font-family: Oswald;
  font-style: normal;
  font-weight: bold;
  color: #0F786B;
  font-size: 28px;
  margin: 10px
}

.header {
  padding-top: 0;
  padding-bottom: 0;
  display: flex;
  justify-content: center;
  height: 12%;
  background-color: #021311;
}

.sticky{
  position: sticky;
  top: 0;
  left: 0;
  right: 0;
  z-index: 10;
}

.selected {
  border-bottom: 3px solid #11bb9a;
  color: #095d4c;
}

.section {
  background-color: #00262E;
}

#Home{
  background-color: #00262E;
  height: 88vh;
  text-align: center;
}

#About {
  height: 92vh;
  text-align: center;
}

#Projects {
  height: 88vh;
  text-align: center;
}

.homeP {
  margin-left: 27%;
  padding-top: 15%;
  font-family: 'Lato';
  font-weight: 400;
  font-size: 40px;
  text-align: center;
  width: 50%;
  vertical-align: middle;
  color: #0F786B;
}

.aboutHeader{
  padding-top: 4%;
  font-family: 'Oswald';
  font-size: 2rem;
  color: #707070;
}

.aboutP{
  margin-top: 4%;
  margin-left: 25%;
  font-family: 'Lato';
  font-size: 2rem;
  color: #0F786B;
  margin-right: 25%;
}

@keyframes fadein {
  0% { opacity: 0; }
  100% { opacity: 1; }
}

.fadeinP{
  transition: opacity ls ease;
  animation: fadein 3s;
  -moz-animation: fadein 3s; /* Firefox */
  -webkit-animation: fadein 3s; /* Safari and Chrome */
  -o-animation: fadein 3s; /* Opera */
}

.hidden{
  display: none;
}